import React, { ChangeEvent, useEffect, useRef, useState } from "react";
import "./style.css";
import { useBoardStore, userLoginUserStore } from "stores";
import { useNavigate, useParams } from "react-router-dom";
import { MAIN_PATH } from "constant";
import { useCookies } from "react-cookie";
import { getBoardRequest } from "apis";
import { GetBoardResponseDto } from "apis/response/board";
import { ResponseDto } from "apis/response";
import { convertUrlsToFile } from "utils";

//          component:              //
export default function BoardWrite() {

  //          state:              //
  const titleRef = useRef<HTMLTextAreaElement | null>(null);
  // 2
  //          state:              //
  const contentRef = useRef<HTMLTextAreaElement | null>(null);
  //          state:              //
  const imageInputRef = useRef<HTMLInputElement | null>(null);

  //          state:   path variable          //
  const { boardNumber } = useParams();

  //          state:  (store, )          //
  const { title, setTitle } = useBoardStore();
  const { content, setContent } = useBoardStore();
  const { boardImageFileList, setBoardImageFileList } = useBoardStore();
  // const { resetBoard } = useBoardStore(); //   //    

  //           state:            //
  const { loginUser } = userLoginUserStore();
  //           state:           //
  const [cookies, setCookies] = useCookies();
  //          state:    URL           //
  const [imageUrls, setImageUrls] = useState<string[]>([]);

  //          function:           //
  const navigate = useNavigate();
  //          function: get board response           //
  const getBoardResponse = (responseBody: GetBoardResponseDto | ResponseDto | null) => {
    if (!responseBody) return;
      const { code } = responseBody;
      if ( code === 'NB') alert('  .');
      if ( code === 'DBE') alert(' .');
      if ( code !== 'SU') {
        navigate(MAIN_PATH());
        return;
      }

      const { title, content, boardImageList, writerEmail } = responseBody as GetBoardResponseDto;
      setTitle(title);
      setContent(content);
      setImageUrls(boardImageList);
      // boardImageList  
      // convertUrlsToFile  , utils 
      //      .
      convertUrlsToFile(boardImageList).then(boardImageFileList => setBoardImageFileList(boardImageFileList));

      if (!loginUser || loginUser.email !== writerEmail) {
        navigate(MAIN_PATH());
        return;
      }

      //   
      if (!contentRef.current) return;
      contentRef.current.style.height = 'auto'; 
      contentRef.current.style.height = `${contentRef.current.scrollHeight}px`;

  }

  //          event handler:              //
  const onTitleChangeHandler = (event: ChangeEvent<HTMLTextAreaElement>) =>{
    const { value } = event.target;
    setTitle(value);

    if (!titleRef.current) return;
    //   
    titleRef.current.style.height = 'auto'; 
    titleRef.current.style.height = `${titleRef.current.scrollHeight}px`;
  } 

  //          event handler:              //
  const onContentChangeHandler = (evnet: ChangeEvent<HTMLTextAreaElement>) =>{
    const { value } = evnet.target;
    setContent(value);
    
    if (!contentRef.current) return;
    //   
    contentRef.current.style.height = 'auto'; 
    contentRef.current.style.height = `${contentRef.current.scrollHeight}px`;
  }

  //          event handler:              //
  const onImageChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
    if (!event.target.files || !event.target.files.length) return;
    const file = event.target.files[0];
    
    //  URL
    const imageUrl = URL.createObjectURL(file); //URL    ,  URL   .
    const newImageUrls = imageUrls.map(item => item); // map 
    newImageUrls.push(imageUrl); 
    setImageUrls(newImageUrls);
    
    //     URL
    const newBoardImageFileList = boardImageFileList.map(item => item); //
    newBoardImageFileList.push(file);
    setBoardImageFileList(newBoardImageFileList);

    //        current.value ''()  .
    if (!imageInputRef.current) return;
    imageInputRef.current.value = '';
  }

  //          event handler:                //
  const onImageUploadClickHandler = () => {
    if (!imageInputRef.current) return;
    imageInputRef.current.click();
  }
  //          event handler:                //
  const onImageCloseButtonClickHandler = (deleteIndex: number) => {
    if (!imageInputRef.current) return;
    imageInputRef.current.value = '';

    const newImageUrls = imageUrls.filter((url, index) => index !== deleteIndex); //    delete   .
    setImageUrls(newImageUrls);

    const newBoardImageFileList = boardImageFileList.filter((file, index) => index !== deleteIndex); //    delete   .
    setBoardImageFileList(newBoardImageFileList);
  }


  //          effect:             //
  useEffect(()=>{
    const accessToken = cookies.accessToken;
    if (!accessToken) {
      navigate(MAIN_PATH());
      return
    }
    if (!boardNumber) return;
    getBoardRequest(boardNumber).then(getBoardResponse);
  },[boardNumber]);


  //          render:              //
  return (

  // 1
  <div id="board-update-wrapper">
    <div className="board-update-container">
      <div className="board-update-box">
        <div className="board-update-title-box">
          <textarea ref={titleRef} className="board-update-title-textarea" rows={1} placeholder=" ." value={title} onChange={onTitleChangeHandler}/>
        </div>
        <div className="divider"></div>
        <div className="board-update-content-box">
          <textarea ref={contentRef} className="board-update-content-textarea" placeholder=" ." value={content} onChange={onContentChangeHandler} />
          <div className="icon-button" onClick={onImageUploadClickHandler}>
            <div className="icon image-box-light-icon"></div>
          </div>
          <input ref={imageInputRef} type="file" accept="image/*" style={{display:'none'}} onChange={onImageChangeHandler} />
        </div>
        <div className="board-update-images-box">
          {imageUrls.map((imageUrl,index) => 
          <div className="board-update-image-box">
            <img className="board-update-image" src={imageUrl}/>
            <div className="icon-button image-close" onClick={() => onImageCloseButtonClickHandler(index)}> 
              <div className="icon close-icon"></div>
            </div>
          </div>
          )}
        </div>

      </div>
    </div>
  </div>
  )
}
 