import { useEffect, useState } from "react";

const usePagination = <T>(countPerPage: number) =>{ // ,   
    
    //   
    //                  state:                        //
    //      , board,comment 
    // usePagination  ,     -> 
    const [totalList, setTotalList] = useState<T[]>([]);  
    //                  state:                        //
    const [viewList, setViewList] = useState<T[]>([]);
    //                  state:                        //
    const [currentPage, setCurrentPage] = useState<number>(1); //  1  .
    
    //                  state:                         //
    const [totalPageList, setTotalPageList] = useState<number[]>([1]); //  1  .
    //                  state:                         //
    const [viewPageList, setViewPageList] = useState<number[]>([1]); //  1  .
    //                  state:                       //
    const [currentSection, setCurrentSection] = useState<number>(1);
    
    //                  state:                       //
    const [totalSection, setTotalSection] = useState<number>(1);

    //                  function:                       //
    const setView = () => {
        const FIRST_INDEX = countPerPage * (currentPage - 1); // 1,
        const LAST_INDEX = totalList.length > countPerPage * currentPage ? countPerPage * currentPage : totalList.length; // 3 .
        const viewList = totalList.slice(FIRST_INDEX, LAST_INDEX);
        setViewList(viewList);
    }
    //                  function:                       //
    const setViewPage = () =>{
        const FIRST_INDEX = 10 * (currentSection - 1);
        const LAST_INDEX = totalPageList.length > 10 * currentSection ? 10 * currentSection : totalPageList.length;
        const viewPageList = totalPageList.slice(FIRST_INDEX, LAST_INDEX);
        setViewPageList(viewPageList);
    };
    
    //                  effect: total list                      //
    useEffect(() => {
        const totalPage = Math.ceil(totalList.length / countPerPage); // Math.ceil  
        const totalPageList: number[] = [];
        for (let page = 1; page <= totalPage; page++) totalPageList.push(page);
        setTotalPageList(totalPageList);
        
        const totalSection = Math.ceil(totalList.length / (countPerPage * 10));
        setTotalSection(totalSection);

        setCurrentPage(1);
        setCurrentSection(1);

        setView(); //  
        setViewPage(); //  
    }, [totalList])

    //                  effect: current page                      //
    useEffect(setView, [currentPage]); // setView , setViewPage   
    //                  effect: current section                      //
    useEffect(setViewPage, [currentPage]); // setViewPage , setView 

    return {
        currentPage, currentSection, viewList, viewPageList,totalSection,
        setCurrentPage,setCurrentSection,setTotalList
        
        // 
        // currentPage, //       ,    ,
        // setCurrentPage, //       , 
        // currentSection, //      ,
        // setCurrentSection, //      ,
        // viewList, //    ,  .
        // viewPageList, //      
        // totalSection, //      ,      ,     
        // setTotalList // 1. API    ,         .  . 
    };
}

export default usePagination;