import Main from "views/Main";
import "./App.css";
import { Route, Routes } from "react-router-dom";
import Container from "layouts/Container";
import Authentication from "views/Authentication";
import Search from "views/Search";
import UserP from "views/User";
import BoardWrite from "views/Board/Write";
import BoardDetail from "views/Board/Detail";
import BoardUpdate from "views/Board/Update";
import { AUTH_PATH, BOARD_DETAIL_PATH, BOARD_PATH, BOARD_UPDATE_PATH, BOARD_WRITE_PATH, MAIN_PATH, SEARCH_PATH, USER_PATH } from "constant";
import { useEffect } from "react";
import { Cookies, useCookies } from "react-cookie";
import { userLoginUserStore } from "stores";
import { getSignInUserRequset } from "apis";
import { GetSignInUserResponseDto } from "apis/response/user";
import { ResponseDto } from "apis/response";
import { User } from "types/interface";

//         component: Application          //
function App() {

  //          state:              //
  const { setLoginUser, resetLoginUser} = userLoginUserStore();

  //          state: cookie           //
  const [cookies, setCookeis] = useCookies();

  //          function: get sign in user response           //
  const getSignInUserresponse = (responseBody: GetSignInUserResponseDto | ResponseDto | null) => {
    if (!responseBody) return;
    const { code } = responseBody;
    if ( code === 'AF' || code === 'NU' || code === 'DBE'){
      resetLoginUser(); 
      return;
    }
    const loginUser: User = { ...responseBody as GetSignInUserResponseDto};
    setLoginUser(loginUser);
  }

  //          effect: accessToken cookie                //
  useEffect(()=>{
    //   ,  
    if (!cookies.accessToken){
      resetLoginUser();
      return;
    }
    getSignInUserRequset(cookies.accessToken).then(getSignInUserresponse);
  },[cookies.accessToken])

  //         render: Application          //
  //description:  : '/'- Main//
  //description:  +  : '/auth - Authentication//
  //description:   : '/search/:searchWord' - Search //
  //description:   : '/user/:userEmail' - User //
  //description:   : '/board/detail/:boardNumber - BoardDetail //
  //description:   : '/board/write' - BoardWrite //
  //description:   : '/board/update/:boardNumber' - BoardUpdate//

  return (
    <Routes>
      <Route element={<Container />}>
        <Route path={MAIN_PATH()} element={<Main />} />
        <Route path={AUTH_PATH()} element={<Authentication />} />
        <Route path={SEARCH_PATH(":searchWord")} element={<Search />} />
        <Route path={USER_PATH(":userEmail")} element={<UserP />} />
        <Route path={BOARD_PATH()}>
          <Route path={BOARD_WRITE_PATH()} element={<BoardWrite />} />
          <Route
            path={BOARD_DETAIL_PATH(":boardNumber")}
            element={<BoardDetail />}
          />
          <Route
            path={BOARD_UPDATE_PATH(":boardNumber")}
            element={<BoardUpdate />}
          />
        </Route>
        {/*    */}
        <Route path='*' element={<h1>404 Not Found</h1>} />
      </Route>
    </Routes>
  );
}

export default App;
